    url: 'juntos-por-las-comunidades-cacaoculturas-de-colombia',
    cover: 'cocoa-friends.jpg',
    title: 'JUNTOS POR LAS COMUNIDADES CACAOCULTURAS DE COLOMBIA',
    date: 'AGOSTO 2019',
    content: `<p>Mejorar el campo e impulsar a las comunidades cacaocultoras de Colombia es el motor de El Efecto Cacao, una alianza entre Luker Chocolate, Fundación Luker, USAID Colombia, Enel-Emgesa, Fundación Saldarriaga Concha y la Universidad EAFIT, que trabaja por el desarrollo rural e inclusivo de las comunidades que cultivan cacao en el Bajo Cauca, Urabá y Centro Sur del Huila.</p>
<img src="/static/media/el-efecto-cacao.bc6f7bc9.jpg" alt="Efecto Cacao">
<p>Para hacer realidad El Efecto Cacao, es necesario que el sector privado y el sector público trabajen de la mano. El sector privado tiene una visión de largo plazo, mayor estabilidad en las políticas y estrategias, capacidad de generar valor a través del mercado, contactos con voluntarios internacionales y, en nuestro caso, Luker Chocolate tiene en su ADN el interés por el productor de cacao, sus familias y sus comunidades.</p>
<p>La Agencia de los Estados Unidos para el Desarrollo Internacional, USAID, tiene un modelo llamado GDA (Alianzas de Desarrollo Global), el cual proporciona ayuda efectiva a los países en desarrollo a través de asociaciones público-privadas. Para ejecutar este modelo, el país debe demostrar que tiene empresas privadas con la capacidad de invertir recursos en fines comunitarios, conectando las soluciones con actividades económicas que produzcan valor compartido. Aquí es donde todos los actores de esta alianza juegan un papel fundamental.</p>
<p>Luker Chocolate y la Fundación Luker buscaron recursos en USAID para contribuir a El Sueño de Chocolate, una visión de largo plazo para liderar desarrollo social y económico en las regiones productoras de cacao. La Fundación Luker, además; brinda de manera transversal sus focos de interés en educación y emprendimiento. A la iniciativa se sumaron la Universidad EAFIT con su experiencia en el mejoramiento de las asociaciones productivas para generar desarrollo en los territorios rurales, la Fundación Saldarriaga Concha con la Universidad del Norte y su Programa “ReHaSer”, el cual busca generar habilidades de resiliencia en los territorios más marginados del país, y Enel-Emgesa que desarrolla proyectos sociales y ambientales en el área de influencia de la Represa del Quimbo.</p>
<h3>PERO, ¿Y CÓMO FUNCIONA EL EFECTO CACAO?</h3>
<p>El centro del acuerdo son los pequeños productores de cacao que, beneficiados con un impacto social y económico, llevan estabilidad a su familia y su comunidad. A este pequeño productor lo apoyamos, pero sin un sentido paternalista. El primer compromiso parte de él, quién tiene que invertir recursos en su finca y realizar todas las prácticas de mejoramiento.  La inversión total es de 35 millones de dólares, los cuales son puestos por todos los actores que forman parte de esta alianza y serán ejecutados en los próximos 5 años, tiempo que dura el proyecto.</p>
<p>Luker Chocolate pone a disposición los cultivos empresariales que tiene en el Urabá y en el Huila, como un modelo de desarrollo rural inclusivo para que los productores reciban soporte técnico, apropien mejores tecnologías y aprovechen los canales de comercialización.</p>
<img src="/static/media/paint-window.40d79a05.jpg" alt="Painting window">
<p>El acuerdo aporta todas las herramientas que, por la experiencia con las comunidades cacaocultoras, sabemos que son útiles: capacitación, extensión agrícola, podas de rehabilitación, semillas de sombra transitoria, análisis de suelos, fertilizantes, asesoría administrativa, técnica y comercial para las asociaciones; soporte en emprendimiento, educación formal para niños, jóvenes y adultos; apoyo psicológico y, finalmente; la relación institucional para gestionar otros apoyos según la necesidad puntual de la población beneficiada.</p>
<h3>¿A DÓNDE QUEREMOS LLEGAR?</h3>
<img src="/static/media/cocoa-friends-4.28b994d9.jpg" alt="Efecto Cacao People">
<p>La meta es incrementar la productividad de 1.340 hectáreas de cacao, fortalecer 17 asociaciones cacaocultoras, acompañar 20 iniciativas de emprendimiento y fortalecer las competencias académicas y socio-emocionales de 1.561 personas.</p>
<p>Estamos convencidos de que este tipo de alianzas entre el sector público y el privado son un claro ejemplo del alcance que pueden tener las buenas prácticas de cooperación y los grandes resultados que se pueden generear cuando se trabaja de la mano. Sin duda alguna, El Efecto Cacao cambiará la vida de muchas familias y será un ejemplo de desarrollo rural para el mundo.</p>
<p>Los invitamos a que estén pendientes de todos los avances del proyecto. No olviden dejarnos sus comentarios aquí abajo.</p>
`,
    autor: { name: 'JULIA OCAMPO', avatar: 'https://media.licdn.com/dms/image/C4E03AQFUGeFloCcVFw/profile-displayphoto-shrink_800_800/0?e=1581552000&v=beta&t=NJf7kTdRi6NTIdp250nSBA81HPnpIPN6KQYUr1Tl0uI', details: 'COP The Cocoa Effect at Fundación Luker', description: '', linkedin: ['https://www.linkedin.com/in/julia-ocampo-4725376/', 'Julia Ocampo'] },
