    breads: [{ href: '/blog', name: 'Blog' }, { href: '/blog/valor-compartido', name: 'Valor Compartido' }],
    url: 'cacao-bosques-y-paz',
    title: 'CACAO, BOSQUES Y PAZ',
    cover: 'banner-cacaos-w.jpg',
    date: 'OCTUBRE 2019',
    content: `<p>En Luker Chocolate, todos los días partimos y buscamos crecer desde nuestro propósito superior: hacer del mundo un lugar mejor, ayudando a otras empresas a crear productos con propósito, utilizando nuestro chocolate como una herramienta de cambio. Este futuro y cambio que soñamos empieza en las regiones productoras de cacao en Colombia, de donde obtenemos el mejor Cacao Fino y de Aroma. Pero no nos queríamos quedar ahí. Por eso, hace unos años, decidimos poner todas nuestras fuerzas y ofrecerle soluciones reales al campo colombiano; de ahí nace “El Sueño de Chocolate”.</p>
<p>El reto es enorme. En Colombia, se produce cacao en 27 de los 32 departamentos, lo que genera una gran dispersión geográfica en la producción y un sin número de barreras para los productores y sus familias. Además, las principales regiones cacaoteras de Colombia han sido históricamente zonas de conflicto armado, minería ilegal, narcotráfico etc.; y por desgracia, hoy en día muchas lo siguen siendo. A pesar de que los obstáculos se presentaran una y otra vez en el camino, nuestro sueño es más grande. Sabemos que solos no podemos y que la llave para alcanzar nuestra meta es el trabajo en equipo; solo así lograremos que “El Sueño de Chocolate” deje algún día de ser una aspiración.</p>
<img src="/static/media/bosque-necocli.12b8874a.jpg" alt="Forest Necoclí">
<p>Un reflejo claro de la unión y los esfuerzos en conjunto es la iniciativa Cacao, Bosques y Paz en Colombia. Este es un proyecto público-privado que tiene como objetivo promover modelos productivos de deforestación cero, que favorezcan la protección y restauración de los bosques en la cadena de suministro del cacao, pero que además, aporten en la construcción de paz de nuestro país.</p>
<p>La iniciativa nace en marzo de 2017 cuando se lanza la Declaración de Intención para la Iniciativa Global Cacao y Bosques, liderada por la World Cocoa Foundation, The Sustainable Trade Initiative (IDH), organizaciones sin ánimo de lucro y más de 35 empresas del sector privado; quienes firmaron los acuerdos macros para la acción conjunta en Ghana y Costa de Marfil, principalmente. En julio de 2018, Colombia se convierte en el primer país de Latinoamérica y tercero en el mundo, en adherirse a la iniciativa y firmar su propio acuerdo.</p>
<img src="/static/media/cocoa-forest.dfaf6862.jpg" alt="Cocoa forest">
<p>Nosotros, Luker Chocolate, fuimos la primera empresa del sector privado en creer y firmar la iniciativa Cacao, Bosques y Paz en Colombia; siendo partícipes de la planeación y estructuración del plan de acción, el cual fue lanzado oficialmente el pasado mes de septiembre en Bogotá. Decidimos ser parte de este proyecto porque sabemos que la base de todo lo que hacemos está en las regiones productoras de cacao, porque esa es nuestra fuente de inspiración y la oportunidad que tenemos para transformar comunidades.</p>
<p>Así entonces, esta iniciativa pone en marcha su plan de acción a 10 años, liderada por los ministerios de Ambiente y Desarrollo Sostenible, Agricultura y Desarrollo Rural, la industria cacaotera representada por Luker Chocolate y la Federación Nacional de Cacaoteros (Fedecacao), The Sustainable Trade Initiative (IDH) y la World Resources Institute (WRI), con el apoyo de la World Cocoa Foundation.</p>
<p>Serán 10 años de trabajo, hombro a hombro, en los cuales se busca alcanzar:</p>
<ul>
<li>45 hitos en total (25 a corto, 12 a mediano y 8 a largo plazo).</li>
<li>76 actividades a 2 años.</li>
<li>51 indicadores de gestión para las actividades a 2 años.</li>
<li>Más de 73 indicadores que medirán el sistema de información.</li>
<li>5 grandes resultados a 2030:</li>
</ul>
<ol>
<li>Modelos productivos de cacao con deforestación cero.</li>
<li>Instrumentos financieros que consoliden modelos de cacao con deforestación cero.</li>
<li>Sistemas de monitoreo, verificación y trazabilidad.</li>
<li>Fortalecimiento de la institucionalidad cacaotera para bosques y paz.</li>
<li>Estrategias de comunicación y transparencia.</li>
</ol>
<p>Para la etapa de implementación de este proyecto, también se unirán las fuerzas de nuevos firmantes como la Embajada de Suiza (SECO), Climate Focus, Wildlife Conservation Society (WCS), Organización Alisos, Rainforest Alliance, Lök Foods, Mariana Cocoa Export, The Nature Conservancy (TNC), WWF y el Centro Internacional de Agricultura Tropical (CIAT).</p>
<p>Cacao, Bosques y Paz es un hito muy importante para la cacaocultura en Colombia y todo lo que hay detrás de ella. Será el primer gran pacto por el desarrollo sostenible de la producción de cacao en el país que harán los tres sectores de la economía. Este proyecto será un aporte muy importante en la protección y conservación de toda la riqueza natural con la que convive el cacao, además; transformará las realidades que hoy en día viven las familias cacaocultoras del país, ayudará a superar dificultades y ofrecerá un mejor futuro para el campo colombiano.</p>
`,
    autor: { name: 'JOSÉ MALDONADO', avatar: 'https://media.licdn.com/dms/image/C5603AQGWmXeWsufD9Q/profile-displayphoto-shrink_800_800/0?e=1581552000&v=beta&t=sPc6xh78N7oYmS61XLxym_XcKLYzzBJ9PSp5kyFCPQk', details: 'SUSTAINABLE SUPPLY ANALYST', description: 'Sustainable Supply Analyst for Luker Chocolate, focused on agricultural development and sustainability projects of the company\'s value chain.Project leader for organizations of cocoa producers and small- scale farmers.', linkedin: ['https://www.linkedin.com/in/jos%C3%A9-miguel-maldonado-v%C3%A9lez-79a818114/', 'Jose Maldonado'] },
