    title: 'PROTEIN PUD, EL SNACK PERFECTO',
    date: 'NOVIEMBRE 2019',
    content: `<p>En el blog de la semana pasada, les contamos sobre nuestra participación en la feria PLMA (Private Label Manufacturing Association), donde nuestros clientes y asistentes al evento podrán vivir la experiencia del Luker Chocolate Market. Además, presentamos una de nuestras últimas innovaciones en producto: Beau Cao, un chocolate que cuida la belleza desde el interior, el cual exhibiremos en el New Product Expo, una plataforma donde solo algunas de las compañías que participarán en la feria podrán exhibir sus últimas innovaciones.</p>
<p>Hoy, el protagonista de este blog es el Protein Pud, el segundo producto innovador que exhibiremos en ese mismo espacio. Una formulación que hemos desarrollado y puesto a disposición de nuestros clientes quienes la deseen tener en su portafolio de marca privada. Aquí les contamos cómo fue la creación de este producto.</p>
<img src="/static/media/protein-pud.e083a995.jpg" alt="Protein pud">
<p>Actualmente, las personas tienen menos tiempo para estar en sus hogares y preparar las comidas del día. A esto, se le suma que hay otras prioridades como su vida social y el cuidado personal. Es por esto que, los snacks se han convertido en la solución perfecta para alargar el tiempo de consumo entre una comida y la otra, pero al mismo tiempo generan satisfacción.</p>
<p>Partiendo del comportamiento de estos consumidores, es que decidimos desarrollar el Protein Pud: un pudín instantáneo de proteina de soya endulzado con stevia que no necesita refrigeración. Un snack perfecto, pues su preparación es rápida, es saludable, delicioso y te hace sentir satisfecho. Su formulación innovadora permite que tan solo agregando un poco de agua fría se obtenga la textura deseada. Además, es un producto hecho a base de plantas, por lo tanto es apto para veganos. Y para hacer que la experiencia del Protein Pud sea aún más memorable, se le pueden agregar diferentes toppings como frutos secos, fruta deshidratada o fresca, nibs de cacao, etc.</p>
<p>Fueron más de 6 meses en los que nuestro equipo de Investigación y Desarrollo trabajó en búsqueda de la formulación adecuada para crear el balance entre textura y sabor. Primero, se buscaron las materias primas que nos dieran la sensación cremosa y suave que queríamos que tuviera el producto. Luego, estaba el reto de que este mantuviera el sabor exquisito del chocolate hecho con Cacao Fino de Aroma que nos caracteriza y que; además; mantuviera la textura. Sin embargo, el reto más importante fue encontrar la forma en que todo lo anterior se mantuviera en una preparación hecha con agua fría, sin que el pudín se resquebrajara o se volviera granuloso.</p>
<p>Más allá de ser indulgente e innovador, el Protein Pud es buena fuente de proteína, uno de los nutrientes con mayor presencia en el cuerpo humano y que es vital en la formación de músculos y cumple un papel fundamental en el buen funcionamiento del sistema inmune. Además, aporta calcio, un complejo de vitaminas como la vitamina A, vitamina B2, vitamina B12; y minerales como hierro, zinc y cobre.</p>
<p>Con la fórmula perfeccionada, la pregunta final era ¿cómo mostrarle al mundo y a nuestros clientes esta innovación? Para esto, desarrollamos dos presentaciones de producto. La primera, ‘on the go’, es la presentación individual, diseñada para las personas que quieran tener un snack y consumirlo en cualquier momento y lugar. La segunda, ‘home presentation’, es ideal para preparar algo delicioso y saludable mientras se está en casa alejado de la rutina.</p>
<p>En esta infografía encontrarás más información sobre nuestro producto:</p>
<img src="/static/media/ficha-protein-pud.b6ddb06e.jpg" alt="Protein pud infographic">
<p>En Luker Chocolate nos sentimos muy orgullosos de presentar nuestras últimas innovaciones y que, tanto el Beau Cao como el Protein Pud, estén presentes en el Idea Supermarket de PLMA.</p>
<p>Si te interesa el Protein Pud, anímate a trabajar de la mano con nosotros; esta innovación podría ser tuya y hacer parte de tu portafolio de marca privada. Si quieres conocer el producto, acércate al Luker Chocolate Market que se encontrará en el North Hall, stand F3011, del 17 al 19 de noviembre en el Rosemont Convention Center de Chicago. Si no vas a asistir a PLMA y te interesa nuestra innovación, escríbenos.</p>
<p>¡Te esperamos!</p>
`,
    autor: { name: 'JOHANNA VERDUGO', avatar: 'https://media.licdn.com/dms/image/C4E03AQEBjQMIzBoosQ/profile-displayphoto-shrink_800_800/0?e=1581552000&v=beta&t=PJFM_xfyNj0lFBOFrXEG8yuGX72o0fnMupSc49dazWw', details: 'Brand and Communications Analyst', description: 'Social communicator with emphasis on journalism from the Pontificia Universidad Javeriana de Bogotá, Colombia.', linkedin: ['https://www.linkedin.com/in/johanna-verdugo-romero-0564a9151/', 'Johanna Verdugo Romero'] },
